import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import { Dropdown } from "carbon-components-react";
import { EventAPI } from "../../../commons/EventAPI";

const dispatchedEvents = [EventAPI.DROPDOWN_CHANGED];

/*
    Example: <srs-dropdown items=[{text: "Item1", value: "item1"}] label="My Dropdown"/>
*/
const SrsDropdown = function (props, ref) {
    const { items, label } = props;
    const dropdownItems = items.map((item, index) => (
        <Dropdown.Item key={index} itemText={item.text} value={item.value} />
    ));

    const onChange = (value) => {
        props.dispatchEvent(EventAPI.DROPDOWN_CHANGED, { payload: {value, id: props.elementid} });
        props.setValue(value);
    }

    return (
      <Dropdown
        id={props.elementid}
        items={dropdownItems}
        label={label}
        onChange={onChange}
      />
    );
}

const styles = `
    
`
const props = [{
    name: "items",
    defaultValue: [],
    type: "object"
}, {
    name: "label",
    defaultValue: "Dropdown",
    type: "string"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsDropdown), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };